<template>
    <div class="main-page" :style="{background: `url(${$config.waploginBgc}) no-repeat`}">
        <div class="login">
            <img class="login-logo" :src="$config.loginLogo" style="margin-bottom: 50rpx;" />
            <div class="login-line">
                <i class="iconfont icon-side-personcenter" />
                <input class="login-input" type="text" placeholder="账号" v-model="input.username" />
            </div>
            <div class="login-line">
                <i class="iconfont icon-login-password" />
                <input class="login-input" type="password" placeholder="密码" v-model="input.password" />
            </div>
            <div class="login-line" v-if="$config.verifyCode">
                <i class="iconfont icon-login-verification  " />
                <input class="login-input" type="text" placeholder="验证码" v-model="input.codeValue" />
                <img class="imgv" :src="'data:image/jpeg;base64,' + img64" @click="getVerifyCode()">
            </div>
            <div class="login-btn" @click="login">登 录</div>
        </div>
    </div>
</template>

<script>
import login from "./login.js"
import md5 from "js-md5"
export default {
    name: "Login",
    mixins: [login],
    props: {},
    components: {},
    data() {
        return {
            img64: "",
            input: {
                username: "",
                password: "",
                codeId: "",
                codeValue: "",
            },
            rules: {
                username: "请输入账号！",
                password: "请输入密码！"
            }
        }
    },
    methods: {
        async login() {
            if (this.$config.verifyCode) this.rules.codeValue = "请输入验证码！"
            await this.$u.checkForm(this.input, this.rules)
            // 初始化appStatus
            sessionStorage.setItem("appStatus", "[]")
            let loginResult = await this._api.getToken({
                client_id: "client",
                client_secret: "secret",
                grant_type: "password",
                username: this.input.username,
                password: md5(this.input.password + "6002E411B72B694B5B76CD62EFFDA56E").toUpperCase(),
                codeId: this.input.codeId,
                codeValue: this.input.codeValue,
                deviceCode: ""
            })

            if (loginResult.errcode) return this.$u.msg(loginResult.errors)

            this.$u.setItem("token", loginResult.access_token)

            this.loginInit(loginResult.access_token)

            // 保存当前成功账号密码
            if (navigator.userAgent.toLowerCase() == "micromessenger") {
                localStorage.setItem("up", JSON.stringify({
                    client_id: "client",
                    client_secret: "secret",
                    grant_type: "password",
                    username: this.input.username,
                    password: md5(this.input.password + "6002E411B72B694B5B76CD62EFFDA56E").toUpperCase(),
                    codeId: this.input.codeId,
                    codeValue: this.input.codeValue,
                    deviceCode: ""
                }))
            }

        },
        getVerifyCode() {
            if (this.$config.verifyCode) {
                this._api.getVerifyCode().then(res => {
                    this.input.codeValue = ""
                    this.img64 = res.data
                    this.input.codeId = res.cid
                })
            }
        },
        defaultAccount() {
            this.input.username = this.$config.defaultUser
            this.input.password = this.$config.defaultPassword
        }
    },
    mounted() {
        this.$u.clear()
        this.getVerifyCode()
        if (process.env.NODE_ENV == "development") this.defaultAccount()
    }
}
</script>

<style lang="scss" scoped>
.main-page {
    display: flex;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    background-size: cover !important;

    .login {
        text-align: center;

        .login-logo {
            width: 260px;
            margin-top: 190px;
        }

        .login-line {
            position: relative;
            width: 280px;
            height: 38px;
            margin-top: 20px;

            .iconfont {
                position: absolute;
                left: 10px;
                top: 8px;
                font-size: 20px;
            }

            .login-input {
                width: 100%;
                background: none;
                outline: none;
                border: 1px solid #fff;
                border-radius: 3px;
                color: #fff;
                line-height: 38px;
                padding: 0 15px 0 40px;
                text-align: left;

                &::placeholder {
                    color: #fff;
                }
            }

            .imgv {
                position: absolute;
                right: 5px;
                top: 5px;
            }
        }

        .login-btn {
            opacity: 0.9;
            width: 280px;
            height: 38px;
            line-height: 38px;
            // background: #409eff;
            margin: 20px auto 0px;
            border-radius: 3px;
            color: #fff;
            background: linear-gradient(#409eff 3%, #42cbf1 90%, #409eff 90%);
            user-select: none;
        }
    }
}
</style>