import Jwt from "jwt-decode";
let mixins = {
  data() {
    return {};
  },

  filters: {},

  methods: {
    async loginInit(token) {
      // 用户基本信息
      let decodeToken = Jwt(token);
      let myInfoResult = await this._api.myInfo();
      let myInfo = myInfoResult.data;
      if (!myInfo) return this.$u.msg("用户基本信息获取失败！");
      myInfo.uid = decodeToken.uid;
      myInfo.role = decodeToken.role;
      this.$u.setItem("myInfo", myInfo);
      // 菜单
      let menuResult = await this._api.menuIndex({
        isWap: 1,
        PageSize: 1000,
      });
      console.log(menuResult)
      if (menuResult.data.length == 0)
        return this.$u.msg(
          "登录失败！当前账号无权限！",
          "/static/img/warning.png"
        );
      let menu = menuResult.data;
      this.$u.setItem("menu", menu);
      // 去首页
      if (this.$config.customHome) {
      	this.$router.push(this.$config.customHome)
      } else {
      	this.$router.push("/Home")
      }
    },
  },
};
export default mixins;
